
import {
  defineComponent, ref, Ref, computed, watch,
} from 'vue';
import {
  APIOutput,
  APIConverted,
  APIVerified,
  APIUnverified,
  Tab,
  resultFilterer,
  getQueryResult,
} from '../lib/datastructures';
import { applyQuery } from '../lib/nli_helpers';

interface RepContainer {
  currKey: string;
  currRep: string;
  reps: string[];
  hot: boolean;
  idx: number;
}

export default defineComponent({
  name: 'Audit',
  setup() {
    const textRecords = ref('');
    const recordsObject: Ref<APIOutput[] | string> = ref('Enter some records to begin!');
    const currTab = ref(Tab.Input);

    watch(textRecords,
      (newValue) => {
        let text = newValue.trim();
        if (text === '' || text === '[]') {
          recordsObject.value = 'Enter some records to begin!';
        }
        let start = text[0];
        if (start === '[' || start === ',') {
          start = '[';
        } else {
          start = `[${start}`;
        }
        let end = text.slice(-1);
        if (end === ']' || end === ',') {
          end = ']';
        } else {
          end = `${end}]`;
        }
        text = `${start}${text.slice(1, -1)}${end}`;
        try {
          recordsObject.value = JSON.parse(text);
        } catch (e) {
          recordsObject.value = 'Record input was invalid JSON';
        }
      });
    const currentRecord = computed(() => {
      if (typeof recordsObject.value === 'string') {
        return recordsObject.value;
      }
      if (recordsObject.value.length === 0) {
        // eslint-disable-next-line
        currTab.value = Tab.Input;
        return 'No more records. Please enter some.';
      }
      return recordsObject.value[0];
    });

    const topQuery = computed(() => {
      const current = currentRecord.value;
      if (typeof current === 'string' || current.type === 'error') {
        return { text: '', link: '' };
      }
      return getQueryResult(current as (APIVerified | APIUnverified));
    });
    const corrected = ref('');
    watch(currentRecord, (newValue) => {
      if (typeof newValue !== 'string' && newValue.type !== 'error') {
        corrected.value = (newValue as APIConverted).converted;
      }
    });

    const loadFile = (files: File[]) => {
      const reader = new FileReader();
      reader.addEventListener('load', () => {
        if (typeof reader.result === 'string') {
          textRecords.value = reader.result;
        }
      });
      reader.readAsText(files[0]);
    };

    const toggleRef = (someRef: Ref<boolean>) => {
      /* eslint-disable no-param-reassign */
      someRef.value = !(someRef.value);
    };

    const showFullRecord = ref(false);
    const toggleFullRecord = () => toggleRef(showFullRecord);
    const showCorrectionInterface = ref(false);
    const toggleCorrectionInterface = () => toggleRef(showCorrectionInterface);
    const showFilters = ref(false);
    const showAdvancedFilters = ref(false);
    const toggleFilters = () => toggleRef(showFilters);

    const filVerified = ref(false);
    const filUnverified = ref(true);
    const filError = ref(false);
    const filDisplay = ref(true);
    const filSearch = ref(true);
    const filNoRecommendation = ref(true);
    const filLikelyTranscription = ref(true);

    const filterResults = computed(
      () : [APIOutput[], APIOutput[]] | null => {
        if (typeof recordsObject.value === 'string') {
          return null;
        }
        const result: APIOutput[] = [];
        const removed: APIOutput[] = [];
        recordsObject.value.forEach((el) => {
          if (resultFilterer(
            el,
            filVerified.value,
            filUnverified.value,
            filError.value,
            filDisplay.value,
            filSearch.value,
            filNoRecommendation.value,
            filLikelyTranscription.value,
          )) {
            result.push(el);
          } else {
            removed.push(el);
          }
        });
        return [result, removed];
      },
    );

    const getExportURL = (obj: APIOutput[], urlVar: string[]) => {
      const data = new Blob(
        [JSON.stringify(obj)], { type: 'application/json' },
      );
      if (urlVar.length !== 0) {
        URL.revokeObjectURL(urlVar[0]);
      }
      urlVar[0] = URL.createObjectURL(data);
      return urlVar[0];
    };

    const remainingURL: string[] = [];
    const exportRemaining = () => (
      filterResults.value === null ? '' : getExportURL(filterResults.value[0], remainingURL)
    );

    const removedURL: string[] = [];
    const exportRemoved = () => (
      filterResults.value === null ? '' : getExportURL(filterResults.value[1], removedURL)
    );

    const applyFilters = () => {
      if (filterResults.value === null) {
        return;
      }
      // eslint-disable-next-line
      textRecords.value = JSON.stringify(filterResults.value[0]);
      showFilters.value = false;
    };

    const keepResultText = computed(() => {
      if (filterResults.value === null) {
        return '';
      }
      return JSON.stringify(filterResults.value[0]);
    });

    const toggleAdvancedFilters = () => toggleRef(showAdvancedFilters);
    const getTitle = (apiOutput: APIOutput) => {
      if (apiOutput.record.title) {
        return apiOutput.record.title[0];
      }
      if (apiOutput.record.isPartOf) {
        return apiOutput.record.isPartOf[0];
      }
      return '[record has no title]';
    };
    const getDate = (apiOutput: APIOutput) => {
      if (apiOutput.record.date) {
        return apiOutput.record.date[0];
      }
      return '';
    };

    const getCreator = (apiOutput: APIOutput) => {
      if (apiOutput.record.creator) {
        return apiOutput.record.creator[0];
      }
      return '';
    };

    const getPublisher = (apiOutput: APIOutput) => {
      if (apiOutput.record.publisher) {
        return apiOutput.record.publisher[0];
      }
      return '';
    };

    const audited: Ref<APIOutput[]> = ref([]);
    const rejected: Ref<APIOutput[]> = ref([]);
    const auditedURL: string[] = [];
    const exportAudited = () => getExportURL(audited.value, auditedURL);
    const rejectedURL: string[] = [];
    const exportRejected = () => getExportURL(rejected.value, rejectedURL);

    const accept = () => {
      if (typeof currentRecord.value === 'string') {
        return;
      }
      audited.value.push(currentRecord.value);
      recordsObject.value = recordsObject.value.slice(1);
      textRecords.value = JSON.stringify(recordsObject.value);
    };
    const reject = () => {
      if (typeof currentRecord.value === 'string') {
        return;
      }
      rejected.value.push(currentRecord.value);
      recordsObject.value = recordsObject.value.slice(1);
      textRecords.value = JSON.stringify(recordsObject.value);
    };
    // eslint-disable-next-line
    const replists = {"type": "text", "output": [{"key": "sefirot", "reps": ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]}, {"key": "tenuah", "reps": ["", "", "", "", "", "", "", "", "", "", "", ""]}, {"key": "ba-derakhim", "reps": ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]}, {"key": "lo", "reps": ["", "", ""]}, {"key": "-", "reps": ["-"]}, {"key": "ironiyot", "reps": ["", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", "", ""]}], "diagnostic_info": {"standard": "New DIN 31631", "foreign_tokens": false, "transliteration_tokens": true, "fully_converted": true, "all_cached": false, "all_recognized": true}};
    const repcontainers = ref(replists.output.map((replist, i) => ({
      currKey: replist.key,
      currRep: replist.reps[0],
      reps: replist.reps.slice(0, 10),
      hot: false,
      idx: i,
    })));
    let currentlyHot = 0;
    const activateEditor = (word: RepContainer) => {
      repcontainers.value[currentlyHot].hot = false;
      word.hot = true;
      currentlyHot = word.idx;
    };
    const isActive = (tab: Tab) => (currTab.value === tab ? 'active' : '');
    const boxSize = (word: RepContainer, ratio: number): string => (
      `max-width: ${Math.max(word.currRep.length, 2) * ratio}rem`
    );
    return {
      textRecords,
      recordsObject,
      currTab,
      isActive,
      Tab,
      currentRecord,
      loadFile,
      corrected,
      accept,
      reject,
      audited,
      rejected,
      showFullRecord,
      toggleRef,
      toggleFullRecord,
      showCorrectionInterface,
      toggleCorrectionInterface,
      showFilters,
      toggleFilters,
      showAdvancedFilters,
      toggleAdvancedFilters,
      getTitle,
      getDate,
      getCreator,
      getPublisher,
      filVerified,
      filUnverified,
      filError,
      filDisplay,
      filSearch,
      filNoRecommendation,
      filLikelyTranscription,
      filterResults,
      exportRemaining,
      exportRemoved,
      exportAudited,
      exportRejected,
      applyFilters,
      keepResultText,
      topQuery,
      repcontainers,
      applyQuery,
      activateEditor,
      boxSize,
    };
  },
});
